.banner{
    background-size: cover;
    height: 550px;
    color: white;

}

.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 50px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button{
    color: white;
    height: 50px;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
    
}

.playbutton{
    color: white;
    height: 50px;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
    padding-left: 20px;
    
}

.playbutton:hover{
    color: black;
    background-color: #e6e6e6;
    padding-left: 20px;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}
.fade_bottom{
    height: 360px;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}

