.navbar{

    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 35px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}

.navlist{
    color: white;
    font-size: 14px;
    margin-left: 170px;
    position: fixed;
    cursor: pointer;
    
    
}

.logo{
    position: fixed;
    left: 50px;
    width: 90px;
    cursor: pointer;
}

.avatar{
    position: fixed;
    right: 80px;
    width: 30px;
    border-radius: 5px;
    cursor: pointer;
}

.navlist :hover{
    color: rgb(197, 30, 30);
    transition: 450ms;
}

.placesearch{
    position: fixed;
    color: white;
    right: 180px;
    cursor: pointer;
}

.downbutton{
    position: fixed;
    color: white;
    right: 60px;
    cursor: pointer;

}

.notification{
    position: fixed;
    color: white;
    right: 130px;
    cursor: pointer;

}


